*, body{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

/* Spacer */
.spacer {
    height: 24px;
}

/* Publication box */
.publication-box {
    width: 60%;
    font-weight: 300;
    font-size: 18px;
    padding: 48px;
    text-align: left;
}

