.header {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 55%;
    height: 60%;
    background: #4696e5;
    border: 2px solid white;
    border-radius: 25px;
}

.header .text-container {
    float: left;
    font-size: 24px;
    max-width: 60%;
    margin-right: 16px;
    padding-right: 32px;
}

.header .text-container p{
    color: #eaeaea;
}

.header .image {
    float: left;
    height: 80%;
    width: auto;
}

.header .image img {
    display: flex;
    height: 100%;
    border: 2px solid white;
    border-radius: 25px;
}

.header .text-container button{
    outline: 0;
    display: inline-block;
    padding: 10px 25px;
    color: white;
    background-color: transparent;
    text-align: center;
    cursor: pointer;
    font-size: 24px;
    font-weight: 600;
    border-radius: 25px;
    border: 1px solid white;
}

.header .text-container button:hover {
    background-color: white;
    color: #4696e5;
}

@media screen and (max-width: 1200px) {
    .header {
        width: 85%;
    }

    .header .text-container {
        font-size: 20px;
        margin: 0px 16px 0px 0px;
        padding: 0px 32px 0px 0px;
    }
    
}

@media screen and (max-width: 768px) {
    .header {
        flex-direction: column-reverse;
        height: 80%;
        width: 60%;
    }
    
    .header .text-container {
        max-width: 100%;
        width: 100%;
        font-size: 18px;
        padding: 32px;
        margin: 0px;
    }
    .header .image {
        width: 175px;
        padding-top: 32px;
    }
    .header .image img {
        width: 100%;
    }
}

@media screen and (max-width: 575px) {
    .header {
        flex-direction: column-reverse;
        height: 80%;
        width: 80%;
    }
    
    .header .text-container {
        max-width: 100%;
        width: 100%;
        font-size: 18px;
        padding: 32px;
    }
    .header .image {
        width: 130px;
        padding-top: 32px;
    }
    .header .image img {
        width: 100%;
        height: auto;
    }
}

/* Spotlight */
.spotlight-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width:100%;
    padding: 24px;
    border: 1px solid #d6d6d6;
}

.spotlight-container .heading {
    text-align: center;
    font-size: 18px;
    padding: 48px;
    color: black;
}

.spotlight {
    flex:1 1;
    display: flex;
    flex-direction: row;
    max-width: 70%;
    justify-content: center;
    align-items: center;
    padding-bottom: 24px;
    text-align: left;
}

.spotlight .image {
    flex: 1 1;
    float: left;
    display: block;
    max-width: 300px;
}

.spotlight .image img {
    padding: 3px;
    border: 1px solid #d6d6d6;
    margin: 32px 0px 32px 0px;
    border-radius: 50%;
    max-width: 80%;
    width: 80%;
}

.spotlight .proj-image {
    flex: 1 1;
    float: left;
    display: block;
    max-width: 300px;
}

.spotlight .proj-image img {
    padding: 3px;
    border: 1px solid #d6d6d6;
    margin: 32px 0px 32px 0px;
    border-radius: 20%;
    max-width: 80%;
    width: 80%;
}

.spotlight .content {
    flex: 1 1;
    float: left;
    display: block;
    max-width: 70%;
    font-size: 24px;
    font-weight: 300;
}

.spotlight .proj-content {
    flex: 1 1;
    float: left;
    display: block;
    max-width: 70%;
    font-size: 18px;
    font-weight: 300;
}

.spotlight .proj-content h4 {
    color: #81847d;
}

.row-reverse    { 
    flex-direction: row-reverse;
    text-align: right;
}  

.spotlight-divider {
    width: 50%;
    border: 1px solid #d6d6d6;
    margin-bottom: 24px;
}

@media screen and (max-width: 1200px) {
    .spotlight .content {
        font-size: 18px;
    }
    .spotlight .proj-content {
        font-size: 16px;
    }
    .publication-box {
        font-size: 16px;
    }
}

@media screen and (max-width: 768px) {
    .spotlight {
        display: block;
        text-align: center;
    }
    .spotlight .image {
        max-width: 100%;
        width: 100%;
        text-align: center;
        margin-bottom: 32px;
    }
    .spotlight .image img {
        max-width: 80%;
        width: 80%;
        margin-bottom: 0px;
    }

    .spotlight .proj-image {
        max-width: 100%;
        width: 100%;
        text-align: center;
        margin-bottom: 32px;
    }
    .spotlight .proj-image img {
        max-width: 80%;
        width: 80%;
        margin-bottom: 0px;
    }
    .spotlight .content {
        max-width: 100%;
        padding: 0px 0px 16px 0px;
    }

    .spotlight .proj-content {
        max-width: 100%;
        padding: 0px 0px 16px 0px;
    }
    .publication-box {
        width: 100%;
    }
    .spotlight-divider {
        width: 80%;
    }
}

