*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

.heading, .gallery-image-landscape, .gallery-image-portrait {
    background: transparent;
}
    
body {
  font-family: 'Lato' sans-serif;
}

.gallery {
  background-image: url(/static/media/hardwood.c2442e4e.jpg);
}

.heading {
    text-align: center;
    font-size: 2.0em;
    font-weight: 600;
    letter-spacing: 1px;
    position: relative;
    padding: 60px 40px 40px 40px;
    color: white;
}

/* Landscape Styling */
.gallery-image-landscape {
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.gallery-image-landscape img {
  height: 300px;
  width: 400px;
  transform: scale(1.0);
  transition: transform 0.4s ease;
}

.img-box-landscape {
  box-sizing: content-box;
  margin: 10px;
  height: 300px;
  width: 400px;
  overflow: hidden;
  display: inline-block;
  color: white;
  position: relative;
  background-color: white;
}

.caption {
  position: absolute;
  bottom: 5px;
  left: 20px;
  opacity: 0.0;
  transition: transform 0.3s ease, opacity 0.3s ease;
}

.transparent-box-landscape {
  height: 300px;
  width: 400px;
  background-color:rgba(0, 0, 0, 0);
  position: absolute;
  top: 0;
  left: 0;
  transition: background-color 0.3s ease;
}

.img-box-landscape:hover img { 
  transform: scale(1.1);
}

.img-box-landscape:hover .transparent-box-landscape {
  background-color:rgba(0, 0, 0, 0.5);
}

.img-box-landscape:hover .caption {
  transform: translateY(-20px);
  opacity: 1.0;
}

.img-box-landscape:hover {
  cursor: pointer;
}

/* Portrait style*/

.gallery-image-portrait {
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.gallery-image-portrait img {
  height: 400px;
  width: 300px;
  transform: scale(1.0);
  transition: transform 0.4s ease;
}

.img-box-portrait {
  box-sizing: content-box;
  margin: 10px;
  height: 400px;
  width: 300px;
  overflow: hidden;
  display: inline-block;
  color: white;
  position: relative;
  background-color: white;
}

.transparent-box-portrait {
  height: 400px;
  width: 300px;
  background-color:rgba(0, 0, 0, 0);
  position: absolute;
  top: 0;
  left: 0;
  transition: background-color 0.3s ease;
}

.img-box-portrait:hover img { 
  transform: scale(1.1);
}

.img-box-portrait:hover .transparent-box-portrait {
  background-color:rgba(0, 0, 0, 0.5);
}

.img-box-portrait:hover .caption {
  transform: translateY(-20px);
  opacity: 1.0;
}

.img-box-landscape:hover {
  cursor: pointer;
}

.caption > p:nth-child(2) {
  font-size: 0.8em;
}

.opacity-low {
  opacity: 0.5;
}
html {
    scroll-behavior: smooth;
    transition-duration: 500ms;
}
/* Top Navigation Bar */
    .topnav, .logo, .link {
        font-family: 'Lato', sans-serif;
        text-decoration: none;
        line-height: 1.5em !important;
        border-bottom: none;
        background-color:white;
    }
    .menu {
        background-color: white;
    }
    /* Top Navigation*/
    .topnav {
    	 width: 100%;
    	 border-bottom: 1px solid black;
    	 border-top: 1px solid black;
    	 text-align: right;
         position: relative;
         padding: 0px 16px;
    }
     .topnav .logo {
    	 position: absolute;
    	 top: 0;
    	 left: 0;
    	 font-size: 18px;
    	 font-weight: 600;
    	 color: black;
    	 padding: 32px;
    	 border-right: 1px solid black;
    }
     .link {
    	 display: inline-block;
    	 padding: 16px 0;
    	 margin: 16px 25px;
    	 position: relative;
    	 text-decoration: none;
    	 color: #0f0f0f;
    	 font-weight: 600;
    	 font-size: 18px;
    }
     .link .bar {
    	 width: 100%;
    	 display: inline-block;
    	 border-bottom: 5px solid black;
    	 position: absolute;
    	 bottom: 0;
    	 left: 0;
    	 opacity: 0;
    	 transition: all 300ms;
    }
     .link:hover {
         color:black;
     }
     .link:hover .bar {
    	 opacity: 1;
    }
    
    .sidenav-button {
          background: transparent;
          color: black;
          display: inline-block;
    	  padding: 18.5px;
    	  margin: 15px 25px;
          text-align: center;
          text-decoration: none;
          font-size: 24px;
    }
    
        .sidenav-button:hover {
            cursor: pointer;
        }
    
    /* Sidebar Navigation */
    
    .sidenav {
      height: 100%;
      width: 0;
      position: fixed;
      z-index: 1;
      top: 0;
      right: 0;
      background-color: white;
      overflow-x: hidden;
      transition: 0.5s;
      padding-top: 60px;
    }
    
    .sidenav a {
      padding: 16px 16px 16px 32px;
      text-decoration: none;
      font-size: 18px;
      font-weight: 600;
      color: black;
      display: block;
      transition: 0.3s;
    }
    
    .sidenav a:hover {
      color: #d6d6d6;
    }
    
    .sidenav .closebtn {
      position: absolute;
      top: 0;
      right: 25px;
      font-size: 48px;
      margin-left: 50px;
      padding: 16px;
      background-color: transparent;
      border: none;
      outline: none;
    }
    
    @media (max-width: 768px) {
        .link {
            display: none;
        }
        .link .bar {
            display: none;
        }
    }
    
    @media (min-width: 769px) {
        .sidenav-button {
            display: none;
        }
        .sidenav {
            display: none;
        }
    }
    
/* Footer */

.footer {
    display: flex;
    background-color: #f7f7f7;
    width:100%;
